canvas{width:100%;height:100%;display:block}body{margin:0;font-family:"Chivo",sans-serif;font-size:.8rem}.navbar{background-color:rgba(220,220,221,0.5);display:flex;flex-direction:row;align-items:center;justify-items:center;height:2rem;padding:0 1rem;font-size:.8rem}.navbar .container{width:100%;display:flex;flex-direction:row;justify-content:space-between}.navbar .container ul{list-style:none;margin:0;padding:0;display:flex;flex-direction:row;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem}.navbar .container ul li{margin:0;padding:0;display:inline-block}.navbar .container ul li a{color:#16181a;text-decoration:none}.navbar .container .title{font-weight:700}.search-container{display:flex;flex-direction:column;align-items:center;justify-items:center;justify-content:center;width:100vw;margin-top:5rem}.search-container input[type="text"]{font-family:"Chivo",sans-serif;font-size:.8rem;display:block;border:none;border-radius:0;background:transparent;color:#16181a;font-weight:200;outline:none;padding:0 1rem;border-bottom:1px solid rgba(150,157,165,0.75);width:50%;height:2rem}.search-container input[type="text"]:focus{background-color:rgba(220,220,221,0.25);border-bottom:1px solid #089ec5;transition:0.3s all}.search-container ::-webkit-input-placeholder{color:#969da5;opacity:1}.search-container :-ms-input-placeholder{color:#969da5;opacity:1}.search-container ::placeholder{color:#969da5;opacity:1}.search-container .results{display:flex;flex-direction:column;align-items:center;justify-items:center;justify-content:center;width:100%}.search-container .results a{width:50%;display:flex;align-content:center;justify-items:center;justify-content:space-between;padding:0 1rem;border-bottom:1px solid rgba(150,157,165,0.25);height:2rem;font-size:.8rem;text-decoration:none;line-height:2rem;color:#16181a}body .graph{z-index:-1000;position:absolute;top:0px;left:0px;width:100vw;min-height:100vh;background-color:#fff}body .details{position:absolute;z-index:1000;top:2rem;left:0rem;width:16rem;height:calc(100vh - 4rem);background-color:rgba(255,255,255,0.95);padding:1rem;transition:0.3s all;display:flex;flex-direction:column;scroll-behavior:smooth;overflow-y:scroll;border-right:1px solid #dcdcdd}body .details h1{font-size:.96rem;margin-top:0;color:#16181a}body .details h1 span{color:#969da5;margin-left:0.5rem;text-transform:uppercase;font-weight:200;font-size:.96rem;border:1px solid #969da5;padding:0.15rem 0.2rem}body .details p{line-height:1.4;font-size:.8rem;color:#5c646c}body .details ul{list-style:none;font-size:.8rem;margin:0;padding:0}body .details ul li{display:block;padding:0.5rem 0}body .details ul li a{color:#089ec5;text-decoration:none}body .details ul li a:hover{opacity:0.5}body .details ul li:not(:last-child){border-bottom:1px solid #dcdcdd}body .details .spacer{height:2rem}body .details .actions{display:flex;flex-direction:row;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem;justify-items:space-between}body .details a{transition:0.2s all}body .details a.download{display:inline-block;background-color:#089ec5;text-decoration:none;color:#fff;padding:0.5rem;text-align:center;margin:0.5rem 0;width:100%}body .details a.edit{display:inline-block;background-color:#dcdcdd;text-decoration:none;color:#5c646c;padding:0.5rem;text-align:center;margin:0.5rem 0;width:100%}body .details a:hover{opacity:0.5;transition:0.2s all}body .details h6{color:#969da5;font-size:0.8rem;text-transform:uppercase;font-weight:200;margin-top:2rem;margin-bottom:0.5rem;letter-spacing:1px}.node-label{font-size:.8rem;padding:0.2em 0.5rem;max-width:10rem;text-align:center;overflow-wrap:normal;color:#16181a}.node-label.default{font-size:.8rem;color:#1985a1;border:1px solid #1985a1;background:#fff}.node-label.active{font-size:.8rem;color:#ffffff;background:#1985a1;z-index:1000}.logo img{position:absolute;bottom:50px;right:50px;width:150px;height:auto;opacity:0.25;transition:0.3s all}.logo img:hover{opacity:1;transition:0.3s all}canvas{width:100%;height:100%